/*=====LANDING PAGE(contact section)========*/
.contact-section{
    // background-color: rgb(186, 213, 226);
    margin-top: 50px;
}
.contact-section .left{
    background-image: url('../../assets/img/cakes/other/velizar-ivanov-rXB9YjOQX8I-unsplash.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
