}
.contact-section form{
    padding: 20px;
}
.contact-section h2{
    font-family: 'Pathway Gothic One', sans-serif;
    color: #3b3a3a;
}
input,textarea,button{
    margin-top: 10px;
    border: 1px solid #3b3b3b;
    padding: 5px;
    border-radius: 6px;
    color: #3b3b3b;
    outline: none;
}
.title{
    margin-left: 90px;
    border-bottom: 5px solid steelblue;
    width: 35px;
    font-family: 'Pathway Gothic One', sans-serif;
}

/*===========Landing Page(featured section)============*/
.featured-section{
    padding: 20px 0px 30px 0px;
}
.featured-section .category{
    display: flex;
}
.featured-section .category-img{
    display: flex;
    flex-wrap: wrap;
}
.featured-section .category-img img{
    width: 250px;
    margin: auto;
    margin-top: 10px;
}
.featured-section .category button{
    margin: 0px 20px 0px 20px;
    border-radius: 20px;
    background-color: white;
    color: #555454;
    border: 1px solid grey;
    width: auto;
    outline: none;

}
.featured-section .category button img{
    margin-right: 3px;
}
.featured-section .featured-cake{
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
    height: 290px;
}
.featured-section .featured-cake:hover{
    opacity: 0.7;
}
.featured-section .modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}
.modal-content {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
}
.modal-content, #caption {
    animation-name: zoom;
    animation-duration: 0.6s;
}
@keyframes zoom {
    from {transform:scale(0)}
    to {transform:scale(1)}
}
.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}
  
.close:hover,
    .close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
}
  
  /* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
    .modal-content {
      width: 100%;
    }
}



